body,
html {
	height: 100%;
	box-sizing: border-box;
}

.none {
	display: none;
}

.content-initial {
	width: 100%;
	height: 100vh;
	margin-bottom: 0;
	background-color: #FB6B6B;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

.content-initial button {
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: 'Recursive', sans-serif;
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;

	/* identical to box height */
	text-align: center;

	color: #D70900;
	margin-bottom: 100px;
}

.initial-recall {
	font-family: 'Righteous';
	font-style: normal;
	font-weight: 400;
	font-size: 36px;
	line-height: 45px;
	display: flex;
	align-items: center;
	text-align: center;
	letter-spacing: -0.012em;
	margin: 40px;

	color: #FFFFFF;
}

.initial-button {
	background: #FFFFFF;
	border: 1px solid #D70900;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
	width: 246px;
	height: 54px;
}

.logo img {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 136px;
	height: 161px;

}

.content-questions{
	width: 100%;
	height: 100vh;
	margin-bottom: 0;
	background-color: #FB6B6B;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

.question-button {
	background: #FFFFFF;
	box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
	border-radius: 5px;
	font-family: 'Recursive', sans-serif;
	padding-left: 15px;
	font-style: normal;
	font-weight: 700;
	font-size: 16px;
	line-height: 19px;
	width: 300px;
	height: 65px;
	color: #333333;
	margin: 40px;
	align-items: center;
	justify-content: space-evenly;
	
}

.content-questions ion-icon{
	font-size: x-large;
}




